.InputContent {
    margin: auto;
	margin-top: 15px;
    width: 90%;
    text-align: center;
    box-shadow: 0 2px 3px #ccc;
    border: 1px solid #eee;
    padding: 10px;
    box-sizing: border-box;
	background-color: #eee;
}

.Input {
    display: block;
    width: 30%;
}

@media (min-width: 600px) {
    .InputContent {
        width: 600px;
    }
}